/* Kurv underside mobil - Stephanie */
.kurvoverblik{
    padding-top: 50px;
    margin-bottom: 50px;

    p {
        font-weight: 800;
        font-size: 22px;
    }

    .kurvcontainer{
        width: 100%;

        justify-content: space-between;

        .kurvindhold {
            .kurvvalg {
                display: flex;
                width: 100%;
                padding: 20px 0;
                border-bottom: $border-bottom;
                justify-content: space-between;

                .kurvcol {
                    height: 200px;
                    display: flex;
                }

                .computer {
                    .kurv1col1{
                        background-image: url("../images/nybox22.png");
                        background-position: center;
                        background-size: cover;
                        width: 100px;
                        height: 100px;
                    }

                    .kurv2col1{
                        background-image: url("../images/nybox1.png");
                        background-position: center;
                        background-size: cover;
                        width: 100px;
                        height: 100px;
                    }

                    .kurvcol2 {
                        display: flex;
                        flex-direction: column;
                        margin-left: 10px;

                        h2 {
                            font-size: 16px;
                        }

                        h3 {
                            font-size: 14px;
                            opacity: 0.7;
                            padding-bottom: 10px;
                        }

                        h5 {
                            color: gray;
                            width: fit-content;
                            cursor: pointer;

                            &:hover {
                                color: red;
                            }
                        }
                    }
                }



                .mobile {
                    flex-direction: column;

                    .kurvcol3{
                        text-align: right;
                        justify-content: end;
                        direction: rtl;
                        flex-direction: column;

                        h3 {
                            font-size: 14px;
                        }

                        input {
                            width: 35px;
                            height: 20px;
                        }
                    }

                    .kurvcol4{
                        justify-content: flex-end;
                        flex-direction: column;

                        h3 {
                            font-size: 14px;
                        }
                    }
                }
            }
        }

        .tjekud{
            .tjekudcontainer{
                background-color: #141C17;
                height: 250px;
                padding: 20px;


                .tjekrow1{
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    p {
                        color: white;
                    }
                }

                .tjekrow2{
                    display: flex;
                    flex-direction: column;
                    padding: 30px 0;

                    .tjekudopstil{
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;

                        h4 {
                            color: white;
                            line-height: 26px;
                            font-size: 14px;
                        }
                    }
                }

                .tjekrow3{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    h3 {
                        color: white;
                        line-height: 26px;
                        font-size: 16px;
                    }
                }
            }

            .betaling{
                background-color: #F2771B;
                width: 100%;
                height: 60px;
                display: flex;
                justify-content: center;
                align-items: center;

                p {
                    color: white;

                    &:hover {
                        transform: scale(1.1);
                        transition: 100ms ease;
                    }
                }
            }
        }
    }

    .accept {
        border-bottom: $border-bottom;
        padding: 30px 0;


        .fab {
            margin-top: 10px;
            font-size: 42px;
            letter-spacing: 10px;
        }
    }

    .kupon {
        padding: 30px 0;
        border-bottom: $border-bottom;

        p {
            span {
                color: #666666;
            }
        }

        input {
            margin-top: 10px;
            width: 100%;
        }

        .brugkode {
            margin-top: 10px;
            cursor: pointer;
            width: 100%;
            display: none;
            background-color: $orange;

            p {
                color: white;
                font-size: 12px;
                padding: 5px 0;

                &:hover {
                    transform: scale(1.1);
                    transition: 100ms ease;
                }
            }
